export default (_self, h) => {
  return [
    h("el-cascader", {
      props: {
        value: _self.obj.value,
        options: _self.obj.items,
        multiple: _self.obj.multiple
      },
      attrs: { 
        placeholder: _self.obj.placeholder,
      },
      on: {
        "change": function(val) {  
          _self.obj.value = val;
          _self.$emit('handleChangeVal', val)
        }
      }
    })
  ];
};


export let cascaderConf = {
  // 
  type: 'cascader',
  // 
  config: true,
  // label
  label: '',
  // 
  inlineBlock: false,
  // 
  require: true,
  // 
  value: [],
  // name
  name: '',
  // 
  multiple: true,
  // 
  items: [{ "value": "1", "label": "1" , children: [{ "value": "1-1", "label": "1-1"}]}, { "value": "2", "label": "2" ,children: []}],
  // 
  inlineBlock: false,
  //
  placeholder: '', 
  // 
  ruleError: '',
  // 
  relation: false,
  // name
  relation_name: '',
  // value
  relation_value: '',
  // 
  visibility: true  
}
