<template> 
  <div>  
    <el-row :gutter="20">   
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span> 
          </div>
          <el-form ref="form" class="b-a" label-width="120px"  v-if="type=='form'">
            <draggable :clone="cloneData" :list="form_list" :options="dragelOptions1">
              <transition-group class="form-list-group draggable-form" type="transition" :name="'flip-list'" tag="div">
                <renders v-for="(items, index) in form_list" :key="index" :ele="items.ele" :obj="items.obj"></renders>
                <!-- <el-slider :value="value1" :key="11515151515"></el-slider> -->
              </transition-group>
            </draggable>
          </el-form>
          <el-form ref="form" class="b-a" label-width="120px" v-if="type=='component'">
            <draggable :clone="cloneData" :list="component_list" :options="dragelOptions1">
              <transition-group class="form-list-group draggable-form" type="transition" :name="'flip-list'" tag="div">
                <renders v-for="(items, index) in component_list" :key="index" :ele="items.ele" :obj="items.obj"></renders>
              </transition-group>
            </draggable>
          </el-form>
        </el-card> 
      </el-col>
      <!--  -->
      <el-col :span="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span></span> 
          </div>
          <el-form ref="form" :model="formData" class="b-a" label-width="150px" > 
            <el-alert
              title="name"
              type="warning"
              :closable="false">
            </el-alert> 
              <draggable :list="sortable_item" :options="dragelOptions2" >
                <transition-group class="form-list-group operation-form" type="transition" :name="'flip-list'" tag="div"> 
                  <renders @handleRemoveEle="removeEle" @handleConfEle="confEle" @changeVisibility="changeVisibility" v-for="(element,index) in sortable_item" :key="index" :index="index" :ele="element.ele" :obj="element.obj || {}" :data="formData" @handleChangeVal="val => handleChangeVal(val,element)" :sortableItem="sortable_item" :config-icon="true">
                  </renders> 
                </transition-group>
              </draggable> 
            <el-form-item>
                <el-button @click="handleSubmit()"></el-button>
              </el-form-item>
          </el-form>
        </el-card> 
      </el-col>
    </el-row>
    <!--  -->
    <el-dialog :title="'' + modalFormData.modalTitle + ''" :visible.sync="dialogVisible">
      <el-form class="form_content" label-width="120px" :model="modalFormData" ref="modalFormData">
        <el-form-item label="" v-if="typeof modalFormData.label != 'undefined'">
          <el-input v-model="modalFormData.label" placeholder="" :maxlength="4"></el-input>
        </el-form-item>
        <el-form-item label="" v-if="dialogVisible"> 
          <el-select v-model="modalFormData.dict" filterable @change="handleDataDictChange">
            <!-- valuejsonparent_namehandleDataDictChangemodalFormData -->
            <el-option v-for="item in dataDictList" :value="JSON.stringify({
              id: item.id, parent_name: item.parent_name})" :key="item.id" :label="item.label"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item 
          label="name" 
          prop="name"  
          :rules="[{ validator: nameValidate, trigger: 'blur' }]"
          >
          <el-input v-model="modalFormData.name" @input="nameInput" placeholder=""></el-input>
        </el-form-item> 
        <!-- <el-form-item label="" v-if="typeof modalFormData.relation != 'undefined'">
          //namerelationList
          <el-checkbox :disabled="!modalFormData.name || !relationList.length" v-model="modalFormData.relation"></el-checkbox>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.relation != 'undefined' && modalFormData.relation">
          <el-select v-model="modalFormData.relation_name" class="inline-block" style="width: 150px" @on-change="_=>modalFormData.relation_value = ''">
            <el-option :disabled="item.obj.name == modalFormData.name" v-for="(item,index) in relationList" :key="index" :value="item.obj.name">{{item.obj.label}}</el-option>
          </el-select>
          <p class="inline-block padder-sm"></p>
          <el-select v-model="modalFormData.relation_value" class="inline-block" style="width: 150px">
            <el-option v-for="(item,index) in relationValue" :key="index" :value="item.label_value">{{item.label_name}}</el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="placeholder" v-if="typeof modalFormData.placeholder != 'undefined'">
          <el-input v-model="modalFormData.placeholder" placeholder="placeholder"></el-input>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.maxLength != 'undefined'">
          <el-input-number v-model="modalFormData.maxLength" placeholder="">
          </el-input-number>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.maxSize != 'undefined'">
          <el-input-number :formatter="value => `${value}kb`" :parser="value => value.replace('kb', '')" v-model="modalFormData.maxSize" placeholder="">
          </el-input-number>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.marginTop != 'undefined'">
          <el-input-number :formatter="value => `${value}px`" :parser="value => value.replace('px', '')" v-model="modalFormData.marginTop" placeholder="">
          </el-input-number>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.marginBottom != 'undefined'">
          <el-input-number :formatter="value => `${value}px`" :parser="value => value.replace('px', '')" v-model="modalFormData.marginBottom" placeholder="">
          </el-input-number>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.details_address != 'undefined'">
          <el-checkbox v-model="modalFormData.details_address"></el-checkbox>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.require != 'undefined'">
          <el-checkbox v-model="modalFormData.require"></el-checkbox>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.ruleError != 'undefined'">
          <el-input v-model="modalFormData.ruleError" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.multiple != 'undefined' && modalFormData.type != 'address'">
          <el-checkbox v-model="modalFormData.multiple"></el-checkbox>
        </el-form-item>
				<el-form-item label="" v-if="typeof modalFormData.fotmatType != 'undefined'">
				  <el-select v-model="modalFormData.fotmatType" clearable placeholder="" @change="changeFotmatType">
				    <el-option label="" value="date"></el-option>
						<el-option label="" value="daterange"></el-option>
						<el-option label="" value="datetime"></el-option>
						<el-option label="" value="datetimerange"></el-option>
						<el-option label="" value="month"></el-option>
						<el-option label="" value="monthrange"></el-option>
						<el-option label="" value="year"></el-option>
				  </el-select>
				</el-form-item>
				<el-form-item label="" v-if="typeof modalFormData.format != 'undefined'">
				  <el-radio-group v-model="modalFormData.format" v-if="modalFormData.fotmatType=='date'||modalFormData.fotmatType=='daterange'">
						<el-radio label="yyyy-MM-dd"></el-radio>
				    <el-radio label="yyyyMMdd"></el-radio>
				  </el-radio-group>
					<el-radio-group v-model="modalFormData.format" v-if="modalFormData.fotmatType=='datetime'||modalFormData.fotmatType=='datetimerange'">
						<el-radio label="yyyy-MM-dd HH:mm:ss"></el-radio>
						<!-- <el-radio label="yyyyMMdd HH:mm:ss"></el-radio> -->
					</el-radio-group>
					<el-radio-group v-model="modalFormData.format" v-if="modalFormData.fotmatType=='month'||modalFormData.fotmatType=='monthrange'">
						<el-radio label="yyyy-MM"></el-radio>
					  <el-radio label="yyyyMM"></el-radio>
					</el-radio-group>
					<el-radio-group v-model="modalFormData.format" v-if="modalFormData.fotmatType=='year'">
						<el-radio label="yyyy"></el-radio>
					  <el-radio label="yyyy"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" v-if="typeof modalFormData.isRange != 'undefined'">
				  <el-select v-model="modalFormData.isRange" clearable placeholder="">
				    <el-option label="" :value="false"></el-option>
				    <el-option label="" :value="true"></el-option>
				  </el-select>
				</el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.inlineBlock != 'undefined'">
          <el-checkbox v-model="modalFormData.inlineBlock"></el-checkbox>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.maxRows != 'undefined'">
          <el-progress v-model="modalFormData.maxRows" :min="2" :max="10"></el-progress>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.level != 'undefined'">
          <el-input-number :max="6" :min="1" v-model="modalFormData.level"></el-input-number>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.color != 'undefined'">
          <el-color-picker v-model="modalFormData.color" />
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.action != 'undefined'">
          <el-input v-model="modalFormData.action" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.filename != 'undefined'">
          <el-input v-model="modalFormData.filename" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.filetype != 'undefined'">
          <el-input v-model="modalFormData.filetype" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="Access-Key" v-if="typeof modalFormData.AccessKey != 'undefined'">
          <el-input v-model="modalFormData.AccessKey" placeholder="Key"></el-input>
        </el-form-item>
        <el-form-item label="Access-Token" v-if="typeof modalFormData.AccessToken != 'undefined'">
          <el-input v-model="modalFormData.AccessToken" placeholder="AuthorizationKey"></el-input>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.maxlength != 'undefined'">
          <el-input-number v-model="modalFormData.maxlength" placeholder=""></el-input-number>
        </el-form-item>
        <el-form-item label="" v-if="typeof modalFormData.max != 'undefined'">
          <el-input-number v-model="modalFormData.max" placeholder=""></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button type="text" @click="handleCancel"></el-button>
        <el-button type="primary" :loading="modalFormData.loading" @click="handleOk('modalFormData')"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import draggable from "vuedraggable";
  import form_list from "../../components/custom_form/FormList";
  import renders from "../../components/custom_form/Render";
  export default {
    name: "CustomForm",
    components: {
      draggable,
      renders
    },
    props: {
      dataDict: {   //
        type: Array, 
        default: [],
        required: true
      },
      sortable_item: { //
        type: Array,
        default: [],
      },
      type: {   // formcomponent
        type: String, 
        default: 'form',
        required: true
      },
      component_list: { //
        type: Array,
        default: [],
        required: true
      }
    },
    data () { 
      return { 
        loading:  false, 
        activeNav:'',//  string
        activeParentNav:null,// id number
        menuData: [],
        form_list, //
        // sortable_item: [], //
        formData: {},
        dialogVisible: false,
        // 
        // bugcolor    
        modalFormData: {
          color: '',
          loading: false
        }, 
        value1:0,   
      };
    },
    computed: {
      // 
      dataDictSelected() {  
        return this.sortable_item.map(v => {
          const obj = JSON.parse(v.obj.dict || '{}');
          return obj.id || -1; //
        })
      },
      // 
      dataDictList() {  
        // filter() 
        return this.dataDict.filter(v => { 
          let bool = false;
          v.type.split(",").forEach((val) => {
            val == this.modalFormData.type ? bool = true : '';
          }) 
          return bool
          
        })
      },
      // 1
      dragelOptions1() { 
        return {
          animation: 0,
          ghostClass: "ghost",
          // 
          group: {
            name: "shared",
            pull: "clone",
            revertClone: false
          },
          // 
          sort: false
        };
      },
      // 2
      dragelOptions2() { 
        return {
          animation: 0,
          ghostClass: "ghost",
          group: {
            // shared,pull
            put: ["shared"]
          }
        };
      },
      // 
      relationList() { 
        // type
        let type = ['select', 'radio', 'checkbox'];
        const arr = this.sortable_item.filter(k => {
          return type.indexOf(k.ele) >= 0 && !!k.obj.name;
        }) 
        return arr;
      },
      // 
      relationValue() {
        const name = this.modalFormData.relation_name;
        let items = [];
        if (!name) return items;
        for (let i in this.sortable_item) {
          if (this.sortable_item[i].obj.name == name) {
            items = this.sortable_item[i].obj.items;
          }
        }
        return items;
      }
    },
    created() {  
    },
    mounted() {  
      // this.getFormDictJson();
    },
    methods: { 
			/* *
			 * 
			 */
			changeFotmatType(val){
				if(val=="date"||val=="daterange"){
					this.modalFormData.format = "yyyy-MM-dd"
				}
				if(val=="datetime"||val=="datetimerange"){
					this.modalFormData.format = "yyyy-MM-dd HH:mm:ss"
				}
				if(val=="month"||val=="monthrange"){
					this.modalFormData.format = "yyyy-MM"
				}
				if(val=="year"){
					this.modalFormData.format = "yyyy"
				}
			},
      /**
       * name
       * @param  {[type]}   rule     [description]
       * @param  {[type]}   value    [description]
       * @param  {Function} callback [description]
       * @return {[type]}            [description]
       */ 
      nameValidate(rule, value, callback) {
        if(!this.nameInput(value)){
          callback(new Error(''));
        }else{
          callback();
        } 
      },  
      /**
       * 
       * @param  {[type]} name [description]
       * @return {[type]}      [description]
       */
      handleSubmit() {
        if(this.sortable_item.length && this._noNameInvalid().length){
          this.$emit('saveCustomForm', this._noNameInvalid())
        }else{
          this.$message.info('name')
        }
      },
      /**
       * name
       * @return {[String]} []
       */
      _noNameInvalid(){ 
        return this.sortable_item.filter( item => {
          return item.obj.name ? true : false;
        }) 
      },
      // https://github.com/SortableJS/Vue.Draggable#clone
      // ,
      cloneData(original) { 
        // 
        return JSON.parse(JSON.stringify(original));
      },
      // 
      handleChangeVal(val, element) {
        this.$set(this.formData, element.obj.name, val);
      },
      /**
       * modalmodal
       * @return {[type]}
       */
      handleCancel() {
        this.dialogVisible = false;
        setTimeout( () => {
          this.modalFormData = {
            color: '',
            loading: false
          };
        }, 500)
      },
      /**
       * modal
       * @param  {String} formName [refName]
       * @return {[type]}          [description]
       */
      handleOk(formName) { 
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const index = this.modalFormData.listIndex;
            this.sortable_item[index].obj = Object.assign({},
              this.sortable_item[index].obj,
              this.modalFormData
            );
            this.handleCancel();
          } else { 
            return false;
          }
        });
        
      },
      // modal,
      confEle(index) {   
        let arr = [];// 
        const list_temp = Object.assign({}, this.sortable_item[index]);
        for (let i in list_temp.obj) {
          arr[i] = list_temp.obj[i];
        }
        this.modalFormData = arr;
        // colormodalFormDatacolor
        if (!list_temp.obj['color']) delete this.modalFormData.color;
        // index
        this.modalFormData.listIndex = index;
        // modal
        this.modalFormData.modalTitle = list_temp.obj.label || "";
        // Vue 
        this.modalFormData = Object.assign({}, this.modalFormData);
        this.dialogVisible = true;
      },
      // 
      removeEle(index) {
        let name = this.sortable_item[index].obj.name;
        this.sortable_item.splice(index, 1);
        if (!name) return;
        for (let i in this.sortable_item) {
          // relationtrue
          if (this.sortable_item[i].obj.relation && this.sortable_item[i].obj.relation_name === name) {
            this.$set(this.sortable_item[i].obj, "relation", false);
            this.$set(this.sortable_item[i].obj, "relation_name", "");
            this.$set(this.sortable_item[i].obj, "relation_value", "");
            break;
          }
        }
      },
      // 
      changeVisibility(index, visibility) {
        this.$set(this.sortable_item[index].obj, 'visibility', visibility);
      },
      // modal
      handleDataDictChange(val) {  
        // valmodalFormData.dict
        const obj = JSON.parse(val);
        // modal_submit
        this.$set(this.modalFormData, 'loading', true);
        this.modalFormData = Object.assign({}, this.modalFormData, {
          typeId: obj.id,
          loading: false, 
          parent_name: obj.parent_name
        }); 
        this.sortable_item[this.modalFormData.listIndex].obj = this.modalFormData;
      },
      /**
       * name
       * @return {[type]} [description]
       */
      nameInput(val){   
        let regu = "^[A-Za-z]+[0-9]*$" 
        var re = new RegExp(regu); 
        if (re.test(val)) {
          return true;
        }else{
          return false;
        }  
      }
    }
  }
</script>
<style lang="less">
  .inline {
    display: inline-block;
  }

  .m-l-lg {
    margin-left: 30px
  }

  .wrapper {
    padding: 15px
  }

  .inline-block {
    display: inline-block;
  }

  .padder-sm {
    padding-right: 10px;
    padding-left: 10px
  }

  .b-a {
    border: 1px solid #ccc;
  }

  .ghost {
    opacity: 0.5;
    background: #c8ebfb;
  }

  .form-list-group {
    min-height: 200px;
    padding: 20px !important;
  }

  /* items move */

  .draggable-form{
    .items,
    .items * {
      cursor: move !important;
    }
  }
  .operation-form{
    .items,
    .items * {
      cursor: pointer !important;
    }
  }

  /*  */

  /* P Hr Title */

  .items .item-icon {
    transition: all 2s ease;
    position: absolute;
    top: -29px;
    right: 0px;
    opacity: 0;
    max-height: 0px;
    overflow: hidden;
  }

  /* form */

  .ivu-form-item.items .item-icon {
    top: -15px;
  }

  /*  */

  .item-icon i {
    cursor: pointer !important;
    margin-right: 5px;
  }

  .items:hover .item-icon {
    transition: inherit;
    opacity: 1;
    max-height: 50px;
  }

  /*  margin-bottom */

  .form_content .ivu-form-item:last-child {
    margin-bottom: 0;
  }


  /*  */

  .ivu-form-item-required .ivu-form-item-label:before {
    content: '';
  }

  .items.sortable-items-required .ivu-form-item-label:before {
    content: '*';
    display: inline-block;
    margin-right: 4px;
    line-height: 1;
    font-family: SimSun;
    font-size: 12px;
    color: #ed3f14;
  }
</style>