export default (_self, h) => {
  let icons = [];
  // 
  if (!!_self.obj.config) {
    icons.push(h('i', {
      class: {'el-icon-setting': true}, 
      on: {
        click() { 
          _self.$emit('handleConfEle', _self.index);
        }
      }
    }));
  }
  // 
  icons.push(h('i', {
    class: {'el-icon-minus': true}, 
    style: {'margin-left': '10px' },
    on: {
      click() {
        _self.$emit('handleRemoveEle', _self.index);
      }
    }
  }));
  const item_icon = h('div', {
    class: {
      'item-icon': true
    }
  }, icons);
  return item_icon;
}
