export default (_self, h) => { 
  return [
    h("el-checkbox-group", {
        props: {
          value: _self.obj.value || []
        },
        on: {
          'input'(arr) {
            _self.obj.value = arr;
            _self.$emit('handleChangeVal', arr)
          }
        }
      },
      _self.obj.items.map(v => {
        return h("el-checkbox", {
          props: {
            label: v.label_value
          }
        }, v.label_name)
      }))
  ];
};

