export default (_self, h) => {
  return [
    h("el-select", {
      props: {
        placeholder: _self.obj.placeholder || "",
        value: _self.obj.value, 
        valueKey: 'label_value'
      }, 
      on: {
        "change": function(val) {   
          _self.obj.value = val;
          _self.$emit('handleChangeVal', val)
        }
      }
    }, _self.obj.items.map(v => {
      return h("el-option", {
        props: {
          label: "" + v.label_name,
          value: v.label_value
        },
      })
    }))
  ];
};


export let selectConf = {
  // 
  type: 'select',
  // 
  config: true,
  // label
  label: '',
  // 
  inlineBlock: false,
  // 
  require: true,
  // 
  value: "",
  // name
  name: '',
  // 
  items: [{ "label_value": "1", "label_name": "1" }, { "label_value": "2", "label_name": "2" }],
  // 
  inlineBlock: false,
  //
  placeholder: '', 
  // 
  ruleError: '',
  // 
  relation: false,
  // name
  relation_name: '',
  // value
  relation_value: '',
  // 
  visibility: true  
}
