export default (_self, h) => {
  return [
    h("el-color-picker", {
      props: { 
        value: _self.obj.value,    
      },
      attrs: {  
      },
      on: {
        "change": function(val) {   
          _self.obj.value = val;
          _self.$emit('handleChangeVal', val)
        }
      }
    })
  ];
};


export let colorPickerConf = {
  // 
  type: 'colorPicker',
  // 
  config: true,
  // label
  label: '',
  // 
  inlineBlock: false,
  // 
  require: false,
  // 
  value: "#409EFF",
  // name
  name: '',      
  // 
  inlineBlock: false, 
  // 
  ruleError: '',
  // 
  relation: false,
  // name
  relation_name: '',
  // value
  relation_value: '',
  // 
  visibility: true  
}
