export default (_self, h) => {
  return [
    h("el-slider", {
      props: {
        value: _self.obj.value, 
        vertical: _self.obj.vertical,
        'show-input':false,
      },
      attrs: {  
      },
      on: {
        "change": function(val) { 
          // console.log(val)  
          // _self.obj.value = val;
          // _self.$emit('handleChangeVal', val)
        },
        "input":function (val) {
          // console.log(val)
          _self.obj.value = val
          _self.$emit('handleChangeVal', val)
        }
      }
    })
  ];
};


