export default (_self, h) => {
  return [
    h("el-date-picker", {
      props: {
        type: _self.obj.fotmatType,
        format: _self.obj.format,
        valueFormat: _self.obj.format,
        value: _self.obj.value, // ,
        'start-placeholder':"",
        'end-placeholder':"",
      }, 
      attrs: {
       
      }, 
      on: { 
        // 
        change: (val)=> { 
          _self.obj.value = val;
          _self.$emit('handleChangeVal', val)
        },  
        input(val){
          _self.obj.value = val;
        }
      }, 
    })
  ];
};


export let datePickerConf = {
  // 
  type: 'datePicker',
  // 
  config: true,
  // label
  label: '',
  // 
  inlineBlock: false,
  // 
  require: false,
  // 
  value:'', 
  // name
  name: '',   
  // ()value
  format: 'yyyy-MM-dd',
  // 
  fotmatType: 'date',// date/datetime datetimerange/daterange year/month/monthrange
	
  // 
  inlineBlock: false,
  //
  placeholder: '', 
  // 
  ruleError: '',
  // 
  relation: false,
  // name
  relation_name: '',
  // value
  relation_value: '',
  // 
  visibility: true  
}
