export default (_self, h) => {
  return [
    h("el-radio-group", {
      props: {
        value: _self.obj.value || "1"
      },
      on: {
        'input' (value) {
          debugger
          _self.obj = Object.assign(_self.obj, {
            value
          });
          _self.$emit('handleChangeVal', value)
        }
      }
    }, _self.obj.items.map(v => {
      return h("el-radio", {
        props: {
          label: v.label_value
        },
      }, v.label_name)
    }))
  ];
};

