export default (_self, h) => {
  return [
    h("el-time-picker", {
      props: {
        value: _self.obj.value,  
        isRange: _self.obj.isRange,
        'start-placeholder':"",
        'end-placeholder':"",
      },
      attrs: {  
      },
      on: {
        "input": function(val) {   
          _self.obj.value = val;
          _self.$emit('handleChangeVal', val)
        }
      }
    })
  ];
};


export let timePickerConf = {
  // 
  type: 'timePicker',
  // 
  config: true,
  // label
  label: '',
  // 
  inlineBlock: false,
  // 
  require: false,
  // 
  value: '',  
  // name
  name: '',  
  isRange: false,
  // 
  inlineBlock: false,
  //
  placeholder: '', 
  // 
  ruleError: '',
  // 
  relation: false,
  // name
  relation_name: '',
  // value
  relation_value: '',
  // 
  visibility: true  
}
