<template>
  <div>
    <el-upload
      class="upload-demo"
      drag
      :action="action"
      :headers="headers"
      :multiple="multiple"
      :limit="10"
      :data="myFileData"
      :before-upload="beforeUpload"
      :on-remove="handleRemove"
      :on-exceed="handleExceed"
      :on-success="handleSuccess"
      :name="name">
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"><em></em></div>
      <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
    </el-upload> 
  </div>
</template>
<script>
export default {
  name: "uploadCustom",
  data() {
    return {
      headers:{},
      formData: {
        file_name: "",
        index: -1
      },
      imgName: "",
      visible: false,
      uploadList: [],
      resultList: [],
    };
  },
  computed:{
    myFileData(){
      return {type:this.filetype,userId:Number(sessionStorage.getItem(this.AccessKey)) || null};
    },
  },
  methods: {
    /**
     * headers
     * @param  {[type]} file [description]
     * @return {[type]}      [description]
     */
    beforeUpload(file) {

      let flag = false //false  true
      const isLtXM = file.size/1024 < this.maxSize;
      if (!isLtXM) { flag = false }else{ flag = true } 
      if (!flag) {
        this.$message.error(` ${this.maxSize}KB!`);
        return false
      }

      try {
        console.log(sessionStorage.getItem(this.AccessToken).split(' ')[0])
        if(sessionStorage.getItem(this.AccessToken)) {
          sessionStorage.getItem(this.AccessToken).split(' ')[0].split('"')[1]=='Bearer'?this.headers['Authorization'] = JSON.parse(sessionStorage.getItem(this.AccessToken) || null):this.headers['Authorization'] = 'Bearer '+JSON.parse(sessionStorage.getItem(this.AccessToken) || null);
        }
      } catch(e) {
        this.$message.error(", !")
        return false
      }

      this.headers['nonce']      = Math.floor(Math.random()*10000000000000000) // 0-10000000000000000
      this.headers['Time-Stamp'] = (new Date()).valueOf() // 
      this.headers['secret']     = "pom-product-client" // 
      this.headers['type']       = "PC"      // 
      let object = {
        'nonce':      this.headers.nonce,
        'Time-Stamp': this.headers['Time-Stamp'],
        'secret':     this.headers.secret,
        'Access-Key': this.headers['Access-Key'],
      }
      let array = []
      for ( let k in object ) {
        array.push(k)
      }
      array.sort()
      let FinalArray = [] //
      array.forEach( (item, index) => {
        FinalArray.push(object[item])
      })
      this.headers['sign'] = window.md5Js(FinalArray.join(""))// sign
    },
    /**
     * 
     * @param  {[type]} files    [description]
     * @param  {[type]} fileList [description]
     * @return {[type]}          [description]
     */
    handleExceed(files, fileList) {
      this.$message.warning(`10`);
    },
    /**
     * 
     * @param  {[type]} file [description]
     * @return {[type]}      [description]
     */
    handleRemove(file, fileList) {
      console.log(file, fileList)
      // uploadList
      // this.uploadList = this.$refs.upload.fileList;
      
      let arr = [];
      fileList.forEach( function(element, index) {
        let file = element.response.data[0]
        arr.push({name:file.fileName,id:file.fileId,path:file.path})
      });
      this.resultList = arr;

      this.$emit('handleUploadsValue', this.resultList);
    },
    /**
     * 
     * @param  {[type]} res      [description]
     * @param  {[type]} file     [description]
     * @param  {[type]} fileList [description]
     * @return {[type]}          [description]
     */
    handleSuccess(response, file, fileList) {
      console.log(response, file, fileList)
      // uploadList
      // this.uploadList = this.$refs.upload.fileList;
      
      if(response&&response.meta.code == 0) {
        if(response.data) {
          let file = response.data[0]
          this.resultList.push({name:file.fileName,id:file.fileId,path:file.path})
        }
      } else {
        this.$message.error(response.meta.message)
      }

      this.$emit('handleUploadsValue', this.resultList);
    },

    handleChangeAlias() {
      this.uploadList[this.formData.index]['file_name'] = this.formData.file_name;
      this.uploadList[this.formData.index]['visible'] = false;
      this.$emit('handleUploadsValue', this.uploadList);
      this.formData = {
        file_name: "",
        index: -1
      };
    },
    handleAlias(item, index) {
      // 
      if (this.formData.index != -1) {
        this.$set(this.uploadList[this.formData.index], 'visible', false);
      }
      this.formData = Object.assign({}, this.formData, {
        file_name: item.file_name || "",
        index,
      });
      this.$set(this.uploadList[index], 'visible', true);
    },
    handleFormatError(file) {
      this.$Notice.warning({
        title: "",
        desc: "" +
          file.name +
          ",  jpg/png."
      });
    },
    handleMaxSize(file) {
      this.$Notice.warning({
        title: "",
        desc: "" + file.name + "" + this.maxSize + "kb"
      });
    },
    handleBeforeUpload() {
      // uploadList
      this.uploadList = this.$refs.upload.fileList;
      const check = this.uploadList.length < 5;
      if (!check) {
        this.$Notice.warning({
          title: "."
        });
      }
      return check;
    }
  },
  mounted() { 
    
  },
  props: {
    defaultList: { //
      type: Array,
      default () {
        return [];
      }
    },
    maxSize: { //
      type: Number,
      default: 2048
    },
    action: { //
      type: String,
      default: ''
    },
    type: { //
      type: String,
      default: 'select'
    },
    name: { //
      type: String,
      default: 'files'
    },
    filename: { //
      type: String,
      default: 'files'
    },
    filetype: { //---
      type: String,
      default: 'processFiles'
    },
    multiple: { //
      type: Boolean,
      default: false
    },
    AccessKey: { //Access-Key
      type: String,
      default: 'Access-Key'
    },
    AccessToken: { //Access-Token
      type: String,
      default: 'Access-Token'
    },
  }
};

</script>
<style>
.demo-upload-list {
  margin-top: 15px;
  display: block;
  line-height: 20px;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  margin-right: 4px;
}

