  border-bottom: 1px solid #ccc;
  border-radius: 0;
  line-height: 19px;
  height: 18px;
}

.demo-upload-list input:focus {
  box-shadow: inherit;
}

.alias-btn {
  margin-right: 10px
}

</style>
